"use client";

import { useMemo, useState } from 'react';

import { AwardIcon, UserIcon, XCircleLightIcon } from '@/icons';

import { ConcertCard } from './ConcertCard';
import { DashboardCard } from './DashboardCard';
import { Tabs } from './Tabs';

type TabKey = 'overview' | 'create';

const CONCERTS = [
  {
    id: 'concert-1',
    name: 'Concert Name 1',
    description:
      'Lorem ipsum dolor sit amet consectetur. Elit purus nam gravida porttitor nibh urna sit ornare a. Proin dolor morbi id ornare aenean non. Fusce dignissim turpis sed non est orci sed in. Blandit ut purus nunc sed donec commodo morbi diam scelerisque.',
    totalSeats: 500,
  },
  {
    id: 'concert-2',
    name: 'Concert Name 2',
    description:
      'Lorem ipsum dolor sit amet consectetur. Elit purus nam gravida porttitor nibh urna sit ornare a. Proin dolor morbi id ornare aenean non. Fusce dignissim turpis sed non est orci sed in. Blandit ut purus nunc sed donec commodo morbi diam scelerisque.',
    totalSeats: 200,
  },
];

export function Dashboard() {
  const [activeTab, setActiveTab] = useState<TabKey>('overview');

  const concerts = useMemo(() => CONCERTS, []);

  return (
    <main className="flex flex-1 flex-col gap-10 px-10 py-10">
      <section className="mx-auto flex w-full max-w-5xl flex-wrap justify-center gap-6 md:flex-nowrap">
        <DashboardCard
          icon={UserIcon}
          text="Total of seats"
          number={500}
          color="#0070A4"
          size="base"
        />
        <DashboardCard
          icon={AwardIcon}
          text="Reserved"
          number={120}
          color="#00A58B"
          size="base"
        />
        <DashboardCard
          icon={XCircleLightIcon}
          text="Cancel"
          number={12}
          color="#F96464"
          size="base"
        />
      </section>

      <section className="mx-auto w-full max-w-5xl space-y-6">
        <Tabs
          overviewLabel="Overview"
          createLabel="Create"
          defaultActive={activeTab}
          onChange={(key) => setActiveTab(key)}
        />

        {activeTab === 'overview' ? (
          <div className="flex flex-col gap-6">
            {concerts.map((concert) => (
              <ConcertCard
                key={concert.id}
                name={concert.name}
                description={concert.description}
                totalSeats={concert.totalSeats}
              />
            ))}
          </div>
        ) : (
          <div className="rounded-2xl border border-dashed border-[#D0D5DD] bg-white p-10 text-center text-[#667085]">
            <h3 className="text-xl font-semibold text-[#1275D1]">Create</h3>
            <p className="mt-3 text-base">
              Start building your next concert experience. Place your form or creation flow here.
            </p>
          </div>
        )}
      </section>
    </main>
  );
}

